import React, { Component} from 'react'
import {Card,Button,message,Upload} from 'antd'
import styles from './index.module.less'
import Exform from './form'
import Extable from './table'
import moment from 'moment'
import {withRouter} from 'react-router-dom';
import { postAction } from '@/axios';
class Step1 extends Component {
    state={
        plan_name:'',
        start_time:0,
        end_time:0,
        fileList: [],
        uploading: false,
        planTaskId:"",
        deliverType:'0',
    }
    componentDidMount(){
        let fillPlanInfo;
        try {
            fillPlanInfo = JSON.parse(window.localStorage.fillPlanInfo);
        } catch (error) {
           console.log(error) 
        }
        if(fillPlanInfo.planTaskId){
            this.setState({planTaskId:fillPlanInfo.planTaskId})
        }
    }
    changeForm=(formValue)=>{
        if(formValue.act_time.length>0){
            this.setState({
                plan_name:formValue.plan_name,
                start_time:moment(formValue.act_time[0]).valueOf(),
                end_time:moment(formValue.act_time[1]).valueOf(),
                deliverType:formValue.deliverType
            });
        }else{
            this.setState({
                plan_name:formValue.plan_name,
                start_time:0,
                end_time:0,
                deliverType:formValue.deliverType
            });
        }
    }
    handleNext=()=>{
        // 
        if(this.state.plan_name === ''){
            return message.warning('')
        }
        if(this.state.start_time === 0 || this.state.end_time === 0){
            return message.warning('')
        }
        let info = {
            planTaskId:this.state.planTaskId,
            planTaskName:this.state.plan_name,
            deliverType:this.state.deliverType,
            taskPlanStartTime:moment(this.state.start_time).format("YYYY-MM-DD HH:mm:ss"),
            taskPlanEndTime:moment(this.state.end_time).format("YYYY-MM-DD HH:mm:ss")
        }
        info = JSON.stringify(info).toString();
        const { fileList } = this.state;
        const formData = new FormData();
        fileList.forEach(file => {
            formData.append('files', file);
        });
        formData.append('info',info);
        postAction("/ybt/planTask/createPlanTask",formData,0,2).then(res => {
            if(res.success){
                const planInfo = JSON.stringify({planTaskId:res.obj,type:this.state.type});
                window.localStorage.setItem("fillPlanInfo",planInfo);
                window.localStorage.setItem('taskDistributionType',this.state.deliverType)
                this.props.history.push("/fillPlan/step2")
            }else{
                message.error(res.obj)
            }
        })
    }
    
    handleCancelPlan=()=>{        
        this.props.history.push('/fillPlan')
    }
    render() {
        const { fileList } = this.state;
        const props = {
            onRemove: file => {
                this.setState(state => {
                    const index = this.state.fileList.indexOf(file);
                    const newFileList = this.state.fileList.slice();
                    newFileList.splice(index, 1);
                    return {
                    fileList: newFileList,
                    };
                });
            },
            beforeUpload: file => {
                console.log(file);
                if(file.size>50*1024*1024){
                    message.warn("50MB");
                    return false;
                }
                this.setState(state => ({
                    fileList: [...state.fileList, file],
                }));
                return false;
            },
            fileList,
        };
        return (
            <div className={styles.main}>
                <Card title=''>
                    <Exform 
                        changeForm={this.changeForm} 
                    />
                    <div style={{margin:'20px 0'}}>
                        <Upload {...props}>
                            <Button type='primary' icon="upload"></Button>
                        </Upload>
                    </div>
                    {this.state.planTaskId?<Extable/>:null}
                </Card>
                <div className={styles.btns}>
                    <Button onClick={this.handleNext} type='primary' style={{marginRight:8}}></Button>
                    <Button onClick={this.handleCancelPlan}></Button>
                </div>
            </div>
            
        )
    }
}
export default withRouter(Step1)