import React, { Component } from 'react'
import {Table,Modal,message} from 'antd';
import { postAction } from '@/axios';
export default class Extable extends Component {
    state={
        columns:[
            {
                title:'',
                dataIndex:'sortIndex',
                align:'center'
            },
            {
                title:'',
                dataIndex:'planAttachmentName',
                align:'center'
            },
            {
                title:'',
                dataIndex:'action',
                align:'center',
                render:(text,record)=>{
                    return <span onClick={()=>{this.handleDelete(record)}} style={{color:'#24695C',cursor:'pointer',textDecoration:'underline'}}></span>
                }
            },
        ],
        data:[
            // {
            //     "planAttachmentId": "1",
            //     "planAttachmentName": "1",
            //     "planAttachmentType": "doc",
            //     "planAttachmentPath": "/doc/",
            //     "planTaskId": "1"
            // }
        ],
    }
    componentDidMount() {
        let fillPlanInfo;
        try {
            fillPlanInfo = JSON.parse(window.localStorage.fillPlanInfo);
        } catch (error) {
           console.log(error) 
        }
        if(fillPlanInfo.planTaskId){
            this.getPlanAttachments(fillPlanInfo.planTaskId)
        }
    }
    //
    getPlanAttachments = (planTaskId) => {
        postAction("/ybt/planTask/getPlanAttachments",{planTaskId}).then(res => {
            if(res.success){
                this.setState({
                    data:res.obj.map((item,index)=>({sortIndex:index+1,...item}))
                })
            }else{
                message.error(res.obj)
            }
        })
    }

    handleDelete=(record)=>{
        Modal.confirm({
            title:'',
            content:'',
            okText:'',
            cancelText:'',
            onOk:()=>{
                //
                let params = [{
                    planAttachmentId:record.planAttachmentId,
                    planAttachmentPath:record.planAttachmentPath,
                    planTaskId:record.planTaskId
                }]
                postAction("/ybt/planTask/deletePlanAttachment",params).then(res => {
                    if(res.success){
                        message.success("");
                        this.getPlanAttachments();
                    }else{
                        message.error(res.obj)
                    }
                })
            }
        })
    }
   
    render() {
        return (
            <Table
                rowKey="sortIndex"
                columns={this.state.columns}
                dataSource={this.state.data}
                pagination={false}
            />
        )
    }
}
