import React, { Component } from 'react'
import {Form,Input,DatePicker,message,Radio  } from 'antd';
import moment from 'moment';
import { postAction } from '@/axios';
const FormItem = Form.Item,{RangePicker}=DatePicker
class Exform extends Component {
    constructor(props){
        super(props);
        this.state = {
            planTaskId:"",
            plan_name:"",
            start_time:null,
            end_time:null,
            deliverType:0,//01
        }
    }
    componentDidMount(){
        let fillPlanInfo;
        try {
            fillPlanInfo = JSON.parse(window.localStorage.fillPlanInfo);
        } catch (error) {
           console.log(error) 
        }
        if(fillPlanInfo.planTaskId){//id
            this.getPlanInfoRequest(fillPlanInfo.planTaskId);
        }
    }
    //id
    getPlanInfoRequest = (planTaskId) => {
        postAction("/ybt/planTask/getPlanTaskById",{planTaskId}).then(res => {
            if(res.success){
                this.setState({
                    plan_name:res.obj.planTaskName,
                    start_time:res.obj.taskPlanStartTime,
                    end_time:res.obj.taskPlanEndTime,
                    deliverType:res.obj.deliverType || 0
                });
                this.props.changeForm({
                    plan_name:res.obj.planTaskName,
                    act_time:[moment(res.obj.taskPlanStartTime),moment(res.obj.taskPlanEndTime)],
                    deliverType:res.obj.deliverType || 0
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        let initTime = [];
        if(this.state.start_time && this.state.end_time){
            initTime = [moment(this.state.start_time),moment(this.state.end_time)]
        }
        return (
            <Form  layout='inline'>
                <FormItem label=''>
                    {
                        getFieldDecorator('plan_name',{
                            initialValue:this.state.plan_name
                        })(<Input placeholder='' style={{width:280}}/>)
                    }
                </FormItem>
                <FormItem label=''>
                    {
                        getFieldDecorator('act_time',{
                            initialValue:initTime
                        })(<RangePicker
                            disabledDate={(current)=> {
                                return current && current < moment().endOf('day').subtract(1, 'day');
                            }}
                            // showTime={{ format: 'HH:mm:ss' }}
                            />)
                    }
                </FormItem>
                <FormItem label=''>
                    {
                        getFieldDecorator('deliverType',{
                            initialValue:this.state.deliverType
                        })(
                            <Radio.Group>
                                <Radio value={0}></Radio>
                                <Radio value={1}></Radio>
                            </Radio.Group>
                        )
                    }
                </FormItem>
            </Form>
        )
    }
}

export default Form.create({
    onValuesChange:(props, changedValues, allValues)=>{
        props.changeForm(allValues)
    }
})(Exform)